module.exports = "/* cosmiclib styling */\n\n\n.CL_assetCode,\n.CL_address\n{\n    display: inline-block;\n    margin: 0.1em;\n    border: 1px solid lightgrey;\n    background-color: #F1F1F1;\n    border-radius: 0.2em;\n    padding: 0.1em;\n    color: #4C4C4C;\n}\n\n.CL_assetCode:hover,\n.CL_address:hover\n{\n    cursor: pointer;\n    background-color: lightgrey;\n}\n\n.CL_asset\n{\n    background-color: ghostwhite;\n    border-left: 0;\n    border-right: 0;\n}\n\n.CL_assetIssuer\n{\n    display: none;\n}\n\n.CL_hash:hover\n{\n    background-color: ghostwhite;\n    cursor: pointer;\n}\n\n.CL_done,\n.CL_signed\n{\n    border: 0.1em solid mediumseagreen;\n    border-radius: 0.2em;\n    background-color: lightgreen;\n    padding: 0.1em;\n    color: black;\n}\n\n.CL_error\n{\n    border: 0.1em solid tomato;\n    border-radius: 0.2em;\n    background-color: salmon;\n    padding: 0.1em;\n    color: black;\n}\n\n/*** Transaction node styling ***/\n\n.CL_transactionNode\n{\n    margin: 0.5em 0;\n}\n\n.CL_transactionNode ul.CL_sideInfo,\n.CL_source\n{\n    border-left: 0.2em solid lightgrey;\n    padding-left: 0.2em;\n    margin: 0;\n    list-style-type: none;\n    list-style-position: inside;\n/*\n    font-size: 0.9em;\n*/\n}\n\n.CL_operation\n{\n    margin: 0.5em 0;\n}\n\n.CL_sourcedOperation\n{\n    border-left: 0.2em solid lightgrey;\n    padding-left: 0.2em;\n    background-color: ghostwhite;\n\n}\n\n/*** Status/Signers node styling ***/\n\n.CL_statusNode,\n.CL_signersNode\n{\n    margin: 0.5em;\n    padding: 0;\n}\n\n.CL_status,\n.CL_threshold\n{\n    margin: 0;\n    font-size: 0.85em;\n    font-weight: bold;\n    text-transform: uppercase;\n    font-stretch: condensed;\n}\n\n.CL_statusNode.CL_error\n{\n    background-color: white;\n    border: none;\n}\n\n.CL_events,\n.CL_signers\n{\n    list-style-type: none;\n    list-style-position: inside;\n    font-size: 0.9 em;\n    padding: 0;\n    margin: 0;\n}\n\n.CL_events li,\n.CL_signers li\n{\n    margin:0 ;\n    border-width: 0;\n    padding-left: 0.2em;\n    border-radius: 0;\n}\n\n.CL_events li.CL_done,\n.CL_signers li.CL_signed\n{\n    border-left: 0.3em solid mediumseagreen;\n\n}\n\n.CL_events li.CL_error\n{\n    border-left: 0.3em solid tomato;\n}\n\n/* Loading animation */\n\n.CL_loadingAnim {\n    display: inline-block;\n    width: 0.7em;\n    height: 0.7em;\n    margin: 0 0.2em;\n    border: 0.2em solid rgba(105,105,105,1);\n    border-top-color: rgba(105,105,105,0.4);\n    border-bottom-color: rgba(105,105,105,0.4);\n    border-radius: 50%;\n    -webkit-animation: CL_spin 1s linear infinite;\n            animation: CL_spin 1s linear infinite;\n}\n\n@-webkit-keyframes CL_spin {\n    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }\n    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }\n}\n\n@keyframes CL_spin {\n    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }\n    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }\n}\n"